export default {
		props:{
			title:{
				type:String,
				default:''
			},
			backtext:{
				type:String,
				default:''
			},
			isBack:{   //是否有返回箭头
				type:Boolean,
				default:true
			},
			isHeight:{   //头部撑开高度
				type:Boolean,
				default:true
			},
			isNavBack:{   //固定返回前面的页面
				type:Boolean,
				default:true
			},
			backUrl:{    //返回到指定页面
				type:String,
				default:'index'
			},
			
		},
		data() {
			return {
				statusBarHeight:'',
				headHeight:'',
			};
		},
		methods:{
			goBack(){
					uni.navigateBack({
					    delta: 1
					})
				
			}
		},
		created() {
			const system = uni.getSystemInfoSync()
			this.statusBarHeight = system.statusBarHeight || 25
			this.headHeight = (system.statusBarHeight || 25) + 45//可调整
		}
	}

